import React from "react";
import ReactDOM from "react-dom";
import "../style/style.css";
import SettingsPopup from "./SettingsPopup";
import { withTranslation } from "react-i18next";

class Aside extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showPopup: false,
    };
  }

  togglePopup() {
    this.setState({
      showPopup: !this.state.showPopup,
    });
  }

  render() {
    const { t } = this.props;
    return (
      <div id="aside">
        <div className="user-image">
          <img src={(this.props.stateUser.image)} />
          <p>{this.props.stateUser.name}</p>
        </div>

        <a href="#" id="tasks-button">
          <i className="big bars icon current">
            <div className="active-circle">{this.props.activeTaskCount}</div>
          </i>
          <p>{t("tasks-text")}</p>
        </a>

        <a href="#" onClick={this.togglePopup.bind(this)}>
          <i className="big cog icon"></i>
          <p>{t("settings-text")}</p>
        </a>

        <a
          id="close-app-btn"
          href="#"
          onClick={() => {
            window.open("about:blank", "_self");
            window.close();
          }}
        >
          <i className="big power off icon"></i>
          <p>{t("close-text")}</p>
        </a>

        {this.state.showPopup ? (
          <SettingsPopup
            onAddNewTask={this.props.onAddNewTask}
            closePopup={this.togglePopup.bind(this)}
          />
        ) : null}
      </div>
    );
  }
}

export default withTranslation()(Aside);
